*{
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  outline: none;
}
body {
  direction: rtl;
  font-family: 'vazir' !important;
  background-color: rgb(230, 230, 230);
}


main{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

main .title{
  font-size: 25px;
  text-align: center;
  padding-bottom: 25px;
  margin: 0 50px;
  border-bottom: 6px solid crimson;
}

.reviews{
  width: 400px;
  margin-top: 50px;
  box-shadow: 0 0 10px rgb(131, 131, 131,0.3);
  padding: 20px;
  background-color: #fff;
  position: relative;
  height: 350px;
  text-align: center;
}

.reviews img{
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 50%;
}

.reviews span{
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: crimson;
  display: flex;
  justify-content: center;
  align-items: center;
}

.quote{
  position: absolute;
  top: 32px;
  left: 163px;
  color: white;
}


.authour{
  color: #333;
  font-style: 16px;
  font-weight: bold;  
}

.job{
  color: crimson;
  padding: 10px;
}

.info{
  color: rgb(87, 87 , 87);
  margin-top: 10px;
}

button{
  color: crimson;
  border: none; 
  background-color: none;
  outline: none;
  cursor: pointer;
}


.review-button{
  position: absolute;
  bottom: 10px;
}


.review-button button{
  margin: 0 10px;
}

.review-button button:nth-of-type(1){
  font-size: 20px;
  background-color: white;
}


.review-button button:nth-of-type(2){
  font-size: 20px;
  background-color: white;
}



.Random-btn{
  background-color: rgba(220,20,60,.2);
  position: absolute;
  bottom: 12px;
  transform: translateX(50%);
  padding: 5px 10px;
  font-family: 'vazir';
}